:root {
  /* COLORS */
  --black-main: #000;
  --dark-blue: #080D2A;
  --white-main: #fff;
  --dark-green: #00E8AD;
  /* FONTS-SIZES */
}

/* FONTS */
/* Apple SD Gothic Neo */
*,
*:before,
*:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Apple SD Gothic Neo", "Arial", "Tahoma", sans-serif;
  font-weight: 300;
  font-style: normal;
  text-rendering: optimizelegibility;
  background: var(--dark-blue);
}

header,
section,
footer,
main {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 144rem;
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 1024px) {
  .no-scroll {
    padding-right: 1.7rem;
  }
}

.overlay {
  position: fixed;
  z-index: 9000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background: var(--light-gray);
  opacity: 0.8;
  will-change: display;
  transition: display 0.5s ease-out;
}
.overlay--active {
  display: block;
}

.fixed {
  position: fixed !important;
  z-index: 200 !important;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 1.2rem 0.8rem 0rem rgba(34, 60, 80, 0.2);
  will-change: auto;
  transition: all 0.5s ease-out;
}
@media (min-width: 1320px) {
  .fixed {
    width: calc(100% - 17px) !important;
  }
}

.fixed-wrap {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  will-change: auto;
  transition: all 0.5s ease-out;
}

.btn {
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  outline: none;
  border: none;
  border-radius: 1.5rem;
}
.btn--transparent {
  padding: 1.7rem 6.7rem;
  font-family: "Apple SD Gothic Neo";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 2rem;
  line-height: 2.4rem;
  color: var(--dark-green);
  border: 0.1rem solid var(--dark-green);
  background: transparent;
}
.btn--green {
  padding: 2.2rem 2.5rem;
  font-family: "Apple SD Gothic Neo";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  font-size: 2.2rem;
  line-height: 2.6rem;
  color: var(--black-main);
  background: var(--dark-green);
}
.btn--yellow {
  position: relative;
  padding: 1.7rem 6.7rem;
  font-family: "Apple SD Gothic Neo";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  font-size: 2rem;
  line-height: 2.4rem;
  color: #E8C300;
  background: transparent;
  border: 0.1rem solid #E8C300;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

